	class Auth {

		constructor(){
			this.init()
	 	}

	 	init(){
	 		this.element = $(this)
	 		this.login = $('.Auth-wrapper')
			this.email = this.login.find('.email-input')
			this.password = this.login.find('.pass-input')
			this.authBtn = this.login.find('.auth-btn')
			this.loginHelpBtn = this.login.find('.helpBtn')
			this.serialize = this.login.serializeArray()

			this.register = $('.register-user-form')
			this.username = this.register.find('.username-field')
			this.regEmail = this.register.find('.email-field')
			this.regPass  = this.register.find('.password-field')
			this.confirmPassword = this.register.find('.confirm-password')
			this.registerSerialize = this.register.serialize()
			this.errors = []

			this._bindEvents()
	 	}

	 	_openLoginHelpModal(callback){
	 		this.loginHelpBtn.click(function(e){
	 			e.preventDefault()
	 			$('#AuthHelperModal').modal('show.bs.modal')
	 		})
	 	}

	 	_bindEvents(){

	 		this.email.bind('click', function(){
	 			alertify.log("Enter your valid email address")
	 		})

	 		this.email.bind('mouseenter', function(){
	 			this.element.focus()
	 		})

	 		this.email.bind('mouseleave', function(){
	 			this.element.blur()
	 			this.password.focus()
	 		})

	 		this.email.bind('keyup', function(){
	 			return this.element.val()
	 		})

	 	}

	 	addError(error){
	 		this.errors.push(error)
	 	}

	}